import {Component, Input, OnInit} from '@angular/core';
import {BaseWidget, EmiProcessType} from '../base-widget';

@Component({
    selector: 'app-finishing-process-widget',
    templateUrl: './finishing-process-widget.component.html',
    styleUrls: ['./finishing-process-widget.component.sass']
})
export class FinishingProcessWidgetComponent implements OnInit, BaseWidget {
    showMore = true;
    processType: EmiProcessType = EmiProcessType.Finishing;
    @Input() width: number;
    @Input() height: number;

    @Input() y: number;
    @Input() x: number;

    constructor() {
        // this.y = 245;
        // this.x = 305;
    }

    ngOnInit() {
    }

}



