export enum EmiProcessType {
    Cutting = 0,
    Embroidery = 1,
    Washing = 2,
    Print = 3,
    Sewing = 4,
    Finishing = 5
}

export interface BaseWidget {
    processType: EmiProcessType;
    x: number;
    y: number;
    width: number;
    height: number;
    showMore: boolean;
}

/**
 * Link input data type
 */
export interface LinkPath {
    /**
     * from point x location
     */
    fx: number;
    /**
     * from point y location
     */
    fy: number;
    /**
     * to point x location
     */
    tx: number;
    /**
     * to point y location
     */
    ty: number;
    /**
     * link status
     */
    active: boolean;
}

export interface EmiProcess {
    width: number;
    height: number;
    x: number;
    y: number;
    processType: EmiProcessType;
}



