import {Component, Input, OnInit} from '@angular/core';
import {BaseWidget, EmiProcessType} from '../base-widget';

@Component({
    selector: 'app-print-process-widget',
    templateUrl: './print-process-widget.component.html',
    styleUrls: ['./print-process-widget.component.sass']
})
export class PrintProcessWidgetComponent implements OnInit, BaseWidget {
    showMore = true;
    processType: EmiProcessType = EmiProcessType.Print;
    @Input() width: number;
    @Input() height: number;

    @Input() y: number;
    @Input() x: number;

    constructor() {
        // this.y = 245;
        // this.x = 305;
    }

    ngOnInit() {
    }
}



