import {Component, Input, OnInit} from '@angular/core';
import {BaseWidget, EmiProcessType} from '../base-widget';

@Component({
    selector: 'app-cutting-process-widget',
    templateUrl: './cutting-process-widget.component.html',
    styleUrls: ['./cutting-process-widget.component.sass']
})
export class CuttingProcessWidgetComponent implements OnInit, BaseWidget {
    showMore = true;
    processType: EmiProcessType = EmiProcessType.Cutting;
    @Input() width: number;
    @Input() height: number;

    @Input() y: number;
    @Input() x: number;

    constructor() {
        // this.y = 245;
        // this.x = 305;
    }

    ngOnInit() {
    }

}



