import { Component, OnInit } from '@angular/core';

import {FormControl} from '@angular/forms';
import {Observable} from 'rxjs/Observable';
import 'rxjs/add/operator/startWith';
import 'rxjs/add/operator/map';
import {HistoryOrder, LandingService, Order} from '../landing.service';
import 'rxjs/add/operator/debounceTime';
import 'rxjs/add/operator/throttleTime';
import 'rxjs/add/operator/distinctUntilChanged';
import 'rxjs/add/operator/switch';
import 'rxjs/add/operator/switchMap';

@Component({
  selector: 'app-order-search',
  templateUrl: './order-search.component.html',
  styleUrls: ['./order-search.component.sass'],
  providers: [LandingService]
})
export class OrderSearchComponent implements OnInit {
  hisStates: HistoryOrder[];

  stateCtrl: FormControl;
  filteredStates: Observable<Order[]>;
  states: Order[];

  constructor(private landingService: LandingService) {
    this.stateCtrl = new FormControl();
    this.filteredStates = this.stateCtrl.valueChanges
      .startWith(null).throttleTime(500).distinctUntilChanged().debounceTime(200)
      .switchMap(state => state ? this.landingService.getProposalOrders(state) : Promise.resolve(this.hisStates.slice() as Order[]));
  }

  ngOnInit(): void {
    this.landingService.getHistoryOrders().then(value => this.hisStates = value);
  }
}



