import {ChangeDetectorRef, Component, Input, OnInit} from '@angular/core';
import {BaseWidget, EmiProcessType} from '../base-widget';

@Component({
    selector: 'app-sew-process-widget',
    templateUrl: './sew-process-widget.component.html',
    styleUrls: ['./sew-process-widget.component.sass']
})
export class SewProcessWidgetComponent implements OnInit, BaseWidget {
    showMore = true;
    processType: EmiProcessType = EmiProcessType.Sewing;
    @Input() width: number;
    @Input() height: number;

    @Input() y: number;
    @Input() x: number;

    constructor() {
        // this.y = 245;
        // this.x = 305;
    }

    ngOnInit() {
    }

}



