import {AfterViewInit, Component, ElementRef, HostListener, OnInit, ViewChild} from '@angular/core';
import {EmiProcess, EmiProcessType, LinkPath} from '../base-widget';


@Component({
    selector: 'app-home-view',
    templateUrl: './home-view.component.html',
    styleUrls: ['./home-view.component.sass']
})
export class HomeViewComponent implements OnInit, AfterViewInit {
    @ViewChild('viewport') viewPort: ElementRef;
    processS: EmiProcess[];
    linkPaths: LinkPath[];

    @HostListener('window:resize', ['$event'])
    resizeHandle(event: Event) {
        this.updateLayout();
    }

    private updateLayout() {
        const unitW = this.viewPort.nativeElement.clientWidth / 5;
        const unitH = this.viewPort.nativeElement.clientHeight / 2;
        const gap = 30;

        this.processS[0].x = unitW * (1 - 1) + gap;
        this.processS[0].y = unitH / 2;
        this.processS[0].width = unitW - gap * 2;
        this.processS[0].height = unitH;

        this.processS[1].x = unitW * (2 - 1) + gap;
        this.processS[1].y = gap;
        this.processS[1].width = unitW - gap * 2;
        this.processS[1].height = unitH;

        this.processS[2].x = unitW * (2 - 1) + gap;
        this.processS[2].y = gap * 2 + unitH;
        this.processS[2].width = unitW - gap * 2;
        this.processS[2].height = unitH;

        this.processS[3].x = unitW * (3 - 1) + gap;
        this.processS[3].y = unitH / 2;
        this.processS[3].width = unitW - gap * 2;
        this.processS[3].height = unitH;

        this.processS[4].x = unitW * (4 - 1) + gap;
        this.processS[4].y = unitH / 2;
        this.processS[4].width = unitW - gap * 2;
        this.processS[4].height = unitH;

        this.processS[5].x = unitW * (5 - 1) + gap;
        this.processS[5].y = unitH / 2;
        this.processS[5].width = unitW - gap * 2;
        this.processS[5].height = unitH;


        this.linkPaths = [
            this.extracted(this.processS[0], this.processS[1], true),
            this.extracted(this.processS[0], this.processS[2], true),
            this.extracted(this.processS[1], this.processS[3], false),
            this.extracted(this.processS[2], this.processS[3], true),
            this.extracted(this.processS[3], this.processS[4], true),
            this.extracted(this.processS[4], this.processS[5], true),
        ];
    }

    ngAfterViewInit(): void {
    }

    constructor() {
    }

    ngOnInit() {
        this.processS = [
            {
                x: 20,
                y: 145,
                width: 150,
                height: 300,
                processType: EmiProcessType.Cutting
            },
            {
                x: 200,
                y: 0,
                width: 250,
                height: 300,
                processType: EmiProcessType.Embroidery
            },
            {
                x: 200,
                y: 280,
                width: 250,
                height: 300,
                processType: EmiProcessType.Print
            },
            {
                x: 400,
                y: 145,
                width: 250,
                height: 300,
                processType: EmiProcessType.Sewing
            },
            {
                x: 600,
                y: 145,
                width: 350,
                height: 300,
                processType: EmiProcessType.Washing
            },
            {
                x: 800,
                y: 145,
                width: 450,
                height: 300,
                processType: EmiProcessType.Finishing
            }
        ];
        this.updateLayout();
    }


    private extracted(p2: EmiProcess, p1: EmiProcess, active: boolean) {
        return {
            fx: p2.x + p2.width,
            fy: p2.y + p2.height / 2,
            tx: p1.x - 10,
            ty: p1.y + p1.height / 2,
            active: active
        };
    }
}



