import {Injectable} from '@angular/core';

export interface Order {
  id: string;
}

export interface HistoryOrder extends Order {
  url: string;
}

export interface ProposalOrder extends Order {
  color: string;
}

@Injectable()
export class LandingService {

  constructor() {
  }

  getHistoryOrders(): Promise<HistoryOrder[]> {
    return Promise.resolve([
      {id: 'HistoryJO' + Math.random(), url: 'path/to/his1'},
      {id: 'HistoryJO' + Math.random(), url: 'path/to/his1'},
      {id: 'HistoryJO' + Math.random(), url: 'path/to/his1'},
      {id: 'HistoryJO' + Math.random(), url: 'path/to/his1'},
      {id: 'HistoryJO' + Math.random(), url: 'path/to/his1'},
    ]);
  }

  getProposalOrders(state: string): Promise<ProposalOrder[]> {
    const re = [];
    for (const c of state) {
      re.push({id: 'JO' + c, color: 'color' + c});
    }
    return Promise.resolve(re);
    /*
    return Promise.resolve([
      {id: 'JO3453454', color: 'color1'},
      {id: 'JO3453433', color: 'color2'},
      {id: 'JO3453422', color: 'color3'},
    ]);*/
  }

}



