import {Component, Input, OnInit} from '@angular/core';
import {BaseWidget, EmiProcessType} from '../base-widget';

@Component({
    selector: 'app-washing-process-widget',
    templateUrl: './washing-process-widget.component.html',
    styleUrls: ['./washing-process-widget.component.sass']
})
export class WashingProcessWidgetComponent implements OnInit, BaseWidget {
    showMore = true;
    processType: EmiProcessType = EmiProcessType.Washing;
    @Input() width: number;
    @Input() height: number;

    @Input() y: number;
    @Input() x: number;

    constructor() {
        // this.y = 245;
        // this.x = 305;
    }

    ngOnInit() {
    }

}



